import { Collapse, Space } from "antd";
import React from "react";
import { QUESTION } from "../../models/events.model";

const Qustions: React.FC<{ questions: QUESTION[] }> = ({ questions }) => {
  return (
    <div className="questions">
      <h1>
        <span className="title">Answeres from the onboarding process</span>
        {questions?.length ? (
          <span className="number">{questions?.length} </span>
        ) : (
          <span className="number">0 of 0</span>
        )}
      </h1>

      {questions?.length ? (
        questions.map((question, index) => (
          <Space direction="vertical" className="mt-2 w-100" key={index}>
            <Collapse
              collapsible="icon"
              items={[
                {
                  key: "1",
                  label: question.title,
                  children: <p>{question.answer}</p>,
                },
              ]}
            />
          </Space>
        ))
      ) : (
        <p>No Question To Show !</p>
      )}
    </div>
  );
};

export default Qustions;
