import { Col, Input, Modal, Row } from "antd";
import React, { useState } from "react";

const AddNote: React.FC<{
  isOpen: boolean;
  setIsOpen: (type: boolean) => void;
  handelSubmit: (note: string) => void;
}> = ({ isOpen, setIsOpen, handelSubmit }) => {
  const [noteText, setNoteText] = useState<string>();

  const handelCancel = (): void => {
    setIsOpen(!isOpen);
  };
  return (
    <>
      <Modal
        okText="Add Note"
        cancelText="Cancel"
        open={isOpen}
        onOk={() => handelSubmit(noteText!)}
        onCancel={handelCancel}
      >
        <div>
          <img src={require("../../assets/img/note-add.jpg")} alt="" title="" />
        </div>
        <main>
          <h1>Add Note </h1>
        </main>
        <Row className="mb-4">
          <Col lg={24}>
            <Input
              size="large"
              placeholder="Note"
              onChange={(e) => setNoteText(e.target.value)}
            />
          </Col>
        </Row>
      </Modal>
    </>
  );
};

export default AddNote;
