import { Button, Col, message, Row } from "antd";
import React, { useEffect, useState } from "react";
import Tabs from "../../../components/tabs/tabs";
import { TABS } from "../../../models/tab";
import HiringProcess from "../components/hiring-process/hiringProcess";
import HiringInfo from "../components/usher-info/hiringInfo";
import { useParams } from "react-router-dom";
import { HITING_PROCESS_NOTES, USHER } from "../../../models/usher.model";
import { Innstance } from "../../../Axios";
import AddNote from "../../../components/AddNote";
import ChangeStatus from "../../../components/change-status/change-status";
import { UsherOptions, USHER_ENUM } from "../../../models/enums/status.enum";

const HiringUshersDetails: React.FC = () => {
  const [tabMenu, setTabMenu] = useState<TABS[]>([
    { name: "Usher Info", width: "50", index: 0 },
    { name: "Hiring process notes ", width: "50", index: 1 },
  ]);
  const [currentTab, setCurrentTab] = useState<number>(0);

  const [usherDetails, setUsherDetails] = useState<USHER>();
  const [hiringNotes, setHiringNotes] = useState<HITING_PROCESS_NOTES[]>();
  const [openAddNote, setOpenAddNote] = useState<boolean>(false);
  const [openStatus, setOpenStatus] = useState<boolean>(false);

  const onClickTab = (idx: number): void => {
    setCurrentTab(idx);
  };

  const { id } = useParams();

  const handelSubmittedAddNote = (note: string) => {
    Innstance.post(`admin/ushers/${+id!}/hiring-notes`, {
      note,
    }).then(() => {
      setOpenAddNote(false);
      getHiringProcessNote(+id!);
      message.success("add successfully !");
    });
  };

  useEffect(() => {
    if (id) {
      getUsherDetails(+id);
      getHiringProcessNote(+id);
    }
  }, []);

  const returTabComponent = (type: number): React.ReactElement => {
    switch (type) {
      case 0:
        return <HiringInfo usher={usherDetails!} />;
      case 1:
        return <HiringProcess hiringNotes={hiringNotes!} />;

      default:
        return <HiringInfo usher={usherDetails!} />;
    }
  };

  const getUsherDetails = (id: number): void => {
    Innstance.get(`/admin/ushers/${+id!}`)
      .then((response) => {
        setUsherDetails(response?.data?.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const onChangeStatusSubmit = (status: USHER_ENUM): void => {
    Innstance.patch(`/admin/ushers/${+id!}/change-status`, {
      status: status,
    })
      .then((response) => {
        message.success("Changed succssfully !");
        setOpenStatus(false);
        setUsherDetails((oldUsher) => ({
          ...oldUsher,
          status: USHER_ENUM[status],
        }));
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const getHiringProcessNote = (usherId: number) => {
    Innstance.get(`admin/ushers/${usherId}/hiring-notes`)
      .then((response) => {
        setHiringNotes(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div className="hiring-ushers">
      <div className="hiring-ushers__header">
        <Row className="mb-1-5">
          <Col lg={16}>
            <Tabs tabs={tabMenu} tabClick={onClickTab} />
          </Col>
          <Col lg={8} className="flex-end gap-10">
            <Button
              type="primary"
              size="large"
              onClick={() => setOpenAddNote(true)}
            >
              Add Note
            </Button>
            <Button size="large" onClick={() => setOpenStatus(true)}>
              Change Status
            </Button>
          </Col>
        </Row>
        <Row gutter={[20, 20]}>
          <Col lg={24}>{returTabComponent(currentTab)}</Col>
        </Row>
        {openAddNote && (
          <AddNote
            setIsOpen={setOpenAddNote}
            isOpen={openAddNote}
            handelSubmit={handelSubmittedAddNote}
          />
        )}

        {openStatus && (
          <ChangeStatus
            isOpen={openStatus}
            setIsOpen={setOpenStatus}
            handelSubmit={onChangeStatusSubmit}
            record={usherDetails}
            statusoption={UsherOptions}
          />
        )}
      </div>
    </div>
  );
};

export default HiringUshersDetails;
